.header{
  background-color: black;
}
.header h2{
  color: white;
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.app{
  width: 15vw;
  /* background-color: rgba(205, 240, 78, 0.993) !important; */
}
.calc{
  background-color: #000;
  width:100% !important;
}
.grid-3 {
    display: grid;
    grid-template-columns: auto auto auto;
    background-color: #000;
    padding: 10px;
    width:fit-content;
  }
  .grid-2 {
    display: grid;
    grid-template-columns: auto auto;
    background-color: #000;
    min-width: 100% !important;
  }
  .grid-1 {
    display: grid;
    grid-template-columns: auto;
    background-color: #000;
    padding: 10px;
    width:fit-content;
  }
  .grid-item {
    background-color: rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(0, 0, 0, 0.9);
    font-size: 1.2rem;
    text-align: center;
    width: 3vw;
  }
.pointer{
  cursor: pointer;
  box-shadow: 0.5px 0.5px 0 0 rgba(125,125,125,0.2);
  border: 0.1rem solid rgba(125,125,125,0.2);
}
.user-display{
  background-color: rgba(205, 240, 78, 0.993);
  box-shadow: 0 10px 0 rgba(205, 240, 78, 0.993);
  min-width: 100px !important;
  border:0.1rem solid black;
  display: flex;
  overflow: hidden;
}
.user-display h3,.user-display h4{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0.2rem;
}
.user-display h3{
  width: 70%;
}
.user-display h4{
  opacity: 0.1;
  z-index: 999;
  width: 50%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.user-display h4:hover,.user-display h3:hover{
  text-overflow: none;
  overflow: visible;
  white-space: nowrap;
  color: black;
  opacity: 1;
  font-weight: bold;
  z-index: 999;
  width: 100%!important;
}
.text-white{
  color: white;
}
.text-left{
  text-align: left;
}
.text-right{
  text-align: right;
}
.text-center{
  text-align: center;
}